import { Backdrops } from "./backdrops";
import { Cast } from "./util/cast";
import { Controls } from "./controls";
import { Costumes } from "./costumes";
import { Entity } from "./entity";
import { Env } from "./env";
import { EventEmitter } from 'events';
import { FunctionChecker } from "./util/functionChecker";
import { Keyboard } from "./io/keyboard";
import { ImageEffective, SoundOption, RotationStyle } from './entityConstant';
import { Loop } from "./controls";
import { MathUtils } from "./math-utils";
import { Monitors } from "./monitor/monitors";
//const Mouse = require('./io/mouse');
import { playGround } from "./playGround";
import { Render } from "./render";
//const RotationStyle = require('./rotationStyle');
import { Sounds } from "./sounds";
import { Sprite } from "./sprite";
import { Stage } from "./stage";
import { StageLayering } from "./stageLayering";
//const TextDraw = require('./text/textDraw');
//const TextOption = require('./text/textOption');
import { Utils } from "./utils";
export class Libs {

    get Backdrops () {
        return Backdrops;
    }
    get Cast () {
        return Cast;
    }
    get Costumes () {
        return Costumes;
    }
    get Controls () {
        return Controls;
    }
    get Entity () {
        return Entity;
    }
    get Env () {
        return Env;
    }
    get EventEmitter () {
        return EventEmitter;
    }
    get FunctionChecker () {
        return FunctionChecker;
    }
    get Keyboard () {
        return Keyboard;
    }
    get ImageEffective () {
        return ImageEffective;
    }
    get SoundOption () {
        return SoundOption;
    }
    get RotationStyle () {
        return RotationStyle;
    }
    get Loop () {
        return Loop;
    }
    get Monitors () {
        return Monitors;
    }
    get MathUtils () {
        return MathUtils;
    }
    /**
     * 指定したkeyが押されているとき TRUE
     * key 省略時は 何かのキーが押されているとき TRUE
     * @param {*} key 
     * @returns TRUE/FALSE
     */
    keyIsDown(key?:string) : boolean{
        const _p =  playGround;
        const _r = _p.runtime;
        if(_r){
            return _r.keyIsDown(key);
        }
        return false;
    }
    /**
     * 指定したkeyが押されていないとき TRUE
     * key 省略時は 何かのキーが押されていないとき TRUE
     * 
     * @param {*} key 
     * @returns TRUE/FALSE
     */
    keyIsNotDown(key?:string) : boolean{
        return !(this.keyIsDown(key));        
    }

    /**
     * 何かのキーが押されているとき TRUE
     * @returns TRUE/FALSE
     */
    anyKeyIsDown() :boolean {
        return this.keyIsDown();
    }
    /**
     * マウスが押されているとき TRUE
     * @returns 
     */
    mouseIsPressed() : boolean {
        const mouse = playGround.stage.mouse;       
        return mouse.down;
    }

    get stageWidth () {
        const _p =  playGround;
        return  _p.$stageWidth;
    }

    get stageHeight () {
        const _p =  playGround;
        return _p.$stageHeight;
    }
    /**
     * get rendering rate object
     */
    get renderRate() : {x:number, y:number} {
        const _p = playGround;
        if(_p.render != null && _p.canvas){
            const _rateX = _p.render.stageWidth / _p.canvas.width;
            const _rateY = _p.render.stageHeight / _p.canvas.height;
            return {x: _rateX, y:_rateY};    
        }
        throw `unable calculate renderRate`;
    }
    /**
     * mousePosition ( on canvas )
     */
    get mousePosition ():{x: number, y: number}{
        const _p = playGround;
        const rate = this.renderRate;
        if(_p.canvas){
            const _mouseX = (_p.stage.mouse.x - _p.canvas.width/2 ) * rate.x;
            const _mouseY = (_p.canvas.height/2 - _p.stage.mouse.y) * rate.y;
            return {x: _mouseX, y: _mouseY};    
        }
        throw `unable calculate mouse position`;
    }
    get randomPoint () :{x: number, y: number}{
        const _p = playGround;
        const randomPointX = (Math.random()-0.5)*_p.$stageWidth;
        const randomPointY = (Math.random()-0.5)*_p.$stageHeight;
        return { x: randomPointX, y: randomPointY };
    }
    get randomDirection () :number {
        const direction = (Math.random()-0.5)* 2 * 360;
        if( direction > 180 ){
            return direction - 180;
        }
        return direction;
    }

    getRandomValueInRange( from: Entity , to:Entity, forceAsDecimal:boolean=false ){
        return Utils.randomizeInRange( from , to, forceAsDecimal);
    }

    get Render () {
        return Render;
    }
    get Sounds () {
        return Sounds;
    }
    get Stage () {
        return Stage;
    }
    get StageLayering () {
        return StageLayering;
    }
    get Sprite () {
        return Sprite;
    }
    // get TextDraw () {
    //     return TextDraw;
    // }
    // get TextOption () {
    //     return TextOption;
    // }
    get Utils () {
        return Utils;
    }

    async wait ( t: number ) {
        await Utils.wait( t );
    }
    async waitWhile( condition, entity) {
        const _condition = (entity)?condition.bind(entity):condition;
        while(_condition()){
            await Utils.wait(Env.pace);
        }
    }
    async waitUntil( condition , entity) {
        const _condition = (entity)?condition.bind(entity):condition;
        for(;;) {
            if( _condition() ) {
                break;
            }
            await Utils.wait(Env.pace);
        }
    }

    toScratchPosition(x, y) {
        // Base position -> canvas 
        const rate = this.renderRate;
        const _x = x * rate.x;
        const _y = y * rate.y;
        return {x: _x, y: _y};
    }

    toActualPosition( x, y ) {

        const rate = this.renderRate;
        const _x = x / rate.x;
        const _y = y / rate.y;
        return {x: _x, y: _y};

    }
    /**
     * change scratch position to local position
     * @param {number} x  scratch x position
     * @param {number} y  scratch y position
     * @returns local position
     */
    scratchToLocalPos( x:number, y:number ): {x:number, y:number} {
        const p = playGround;
        if(p.render){
            const w = p.render.stageWidth;
            const h = p.render.stageHeight;
    
            let localPosX = x + w / 2;
            let localPosY = h / 2 - y;
            return {x: localPosX, y: localPosY};    
        }
        throw `unable calculate scratchToLocalPos`;
    }

    /**
     * change local position to scratch position
     * @param {number} x  local x position
     * @param {number} y  local y position
     * @returns {x:number, y:number} scratch position
     */
    localToScratchPos( x:number, y:number ) : {x:number, y:number}{
        const p = playGround;
        if(p.render){
            const w = p.render.stageWidth;
            const h = p.render.stageHeight;
            let scratchPosX = x - w / 2;
            let scratchPosY = h / 2 - y;    
            return {x: scratchPosX, y: scratchPosY};
        }
        throw 'unable calculate localToScratchPos';
    }

    *Iterator(n: number) {
        for(let i=0; i<n; i++){
            yield(i);
        }
    }
    private static _instance: Libs;
    static getInstance(): Libs {
        if( Libs._instance == undefined ) {
            Libs._instance = new Libs();
        }
        return Libs._instance;
    }

    constructor () {
    }
}
export const libs = Libs.getInstance();